  background-color: whitesmoke;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 500px;
}

h1 {
  margin-bottom: 10px;
}

input,
button,
select {
  height: 50px;
}

span[aria-label="required"] {
  color: red;
}

button {
  background-color: black;
  color: white;
  border: 0;
}
.error-wrapper {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  height: 50px;
}

.error-wrapper img {
  max-width: 1.5rem;
}

.error {
  color: red;
}

input:user-invalid,
select:user-invalid {
  border: 2px solid red;
}

input:user-valid,
select:user-valid {
  border: 2px solid green;
}
